import React from 'react'
import PropTypes from 'prop-types'
import { Grid, GridColumn, GridContainer } from '../Grid'
import { Title } from '../Title'
import "./taas-grid.scss";

const TaasGrid = ({ TaasGridElement, internalTitle }) => {
  return (
    <GridContainer >
      <Grid>
        <GridColumn
          columns="12"
          className="taas-grid"
        >
          {internalTitle && <Title variant="lead" className="taas-grid__title">{internalTitle}</Title>}
          {TaasGridElement && TaasGridElement.map(({ title, content, id }) => (
            <GridColumn columns="6" key={id} className="taas-grid__column">
              <Title className="taas-grid__column-title" variant="featuredTitle">
                {title}
              </Title>
              <p
              className="taas-grid__column-content"
              >
                {content}
              </p>
            </GridColumn>
          ))}
        </GridColumn>
      </Grid>
    </GridContainer>
  )
}
export default TaasGrid

TaasGrid.propTypes = {
    TaasGridElement: PropTypes.array,
    internalTitle: PropTypes.string,
}
