import {arrayOf, shape, string} from 'prop-types'
import React from 'react';
import Cta from "../Cta";
import {Title, TitleAccent} from "../Title";
import "./jobs-feed.scss";

const renderJobElement = (item, index) => {
  return (
    <div className="jobs-feed__job" key={index}>
      <Title variant="cardTitleSmall" className="jobs-feed__job-title">
        <TitleAccent>{item.title}</TitleAccent>
      </Title>

      <div className="jobs-feed__job-location">{item.location}</div>
      <div className="jobs-feed__job-location2">{item.location2}</div>

      <Cta to={item.url} variant="readmore-yellow" className="jobs-feed__job-readmore">
        read more
      </Cta>
    </div>
  );
};

function JobsFeed({jobs}) {
  const renderedJobs = jobs.map((article, index) => renderJobElement(article, index));

  return (
    <div className="jobs-feed">
      <div className="jobs-feed__content">
      {renderedJobs}
      </div>
      <div className="jobs-feed__info">or send us your resume <strong>hr@weknowinc.com</strong></div>
    </div>
  )
}

JobsFeed.propTypes = {
  jobs: arrayOf(
    shape({
      title: string.isRequired,
      location: string.isRequired,
      location2: string.isRequired,
      link: string.isRequired,
    }),
  ).isRequired,
};

export default JobsFeed;
